.contact-main{
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}


.contact-border {
  border: black solid 2px;
  margin: 10px;
  width: 20%;
}
.contact-img {
  margin: 5vh 0 0;
  max-width: 200px;
  height: auto;
  cursor: pointer;
}

.contact-main h3{
  font-weight: 500 !important;
  cursor: pointer;
}